html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow-x: hidden; /* Prevent horizontal scrolling */
  box-sizing: border-box; /* Ensure padding and borders are included in element width */
  max-width: 100vw; /* Prevent components from exceeding viewport width */
  /* Removed max-height to prevent unnecessary white space */
}

/* Container to ensure full viewport height but avoid extra white space */
.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Ensures the content covers full height */
}

.footer {
  margin-top: auto; /* Push footer to bottom */
}

/* Media queries for responsiveness */
@media (max-width: 768px) {
  .container {
    padding: 15px; /* Reduce padding on smaller screens */
  }
}

@media (max-width: 480px) {
  .container {
    padding: 10px; /* Further reduce padding for very small screens */
  }
}
